.MessageBox {
  position: fixed;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  width: 100%;
  height: 100%;
  font-family: 'Work Sans', sans-serif;
  font-weight: 500;
  color: #fff;
}

.MessageBox h1 {
  background: transparent;
  font-size: 1.5rem;
}

.MessageBox.off {
  display: none;
}

.Message-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
  width: 50%;
  height: 30%;
  background-color: rgba(124, 33, 117, 0.9);
  -webkit-box-shadow: 0 4px 2px 2px #000000;
          box-shadow: 0 4px 2px 2px #000000;
}

.MessageBox a {
  cursor: pointer;
  width: 8.11rem;
  padding: .77rem 0;
  border-radius: 54px;
  background-color: #010c3f;
  text-align: center;
  font-size: .833rem;
  -webkit-transition: background-color .25s;
  -o-transition: background-color .25s;
  transition: background-color .25s;
}

.MessageBox a:hover {
  background-color: rgba(108, 65, 233, .7);
}



